<template>

  <div>
    <el-form :inline="true">
      <el-form-item>
        <el-input
            v-model="searchForm.fieldName"
            placeholder="请输入模型名称"
            clearable
        >
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="getFieldList">搜索</el-button>
      </el-form-item>

      <el-form-item>
        <!--				<el-button type="primary" @click="dialogVisible = true" v-if="hasAuth('sys:field:save')">新增</el-button>-->
        <el-button type="primary" @click="dialogVisible = true" >新增</el-button>
      </el-form-item>
      <el-form-item>
        <el-popconfirm title="这是确定批量删除吗？" @confirm="delHandle(null)">
          <!--					<el-button type="danger" slot="reference" :disabled="delBtlStatu" v-if="hasAuth('sys:user:delete')">批量删除</el-button>-->
          <el-button type="danger" slot="reference" :disabled="delBtlStatu" >批量删除</el-button>
        </el-popconfirm>
      </el-form-item>
    </el-form>

    <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        border
        stripe
        @selection-change="handleSelectionChange">

      <el-table-column
          type="selection"
          width="55">
      </el-table-column>

      <el-table-column
          prop="id"
          label="ID"
          width="50">
      </el-table-column>

      <el-table-column
          prop="modelName"
          label="模型名称">
      </el-table-column>

      <el-table-column
              prop="parentModelId"
              label="父模型">
      </el-table-column>

      <el-table-column
          prop="fieldTreeId"
          label="字段集">
      </el-table-column>

      <el-table-column
          prop="index"
          label="索引"
          width="200">
      </el-table-column>



      <el-table-column
          prop="remark"
          label="备注">
      </el-table-column>

      <el-table-column
          prop="icon"
          width="260px"
          label="操作">

        <template slot-scope="scope">
          <!--					<el-button type="text" @click="view(scope.row)" v-if="hasAuth('sys:field:view')">查看</el-button>-->
          <el-button type="text" @click="view(scope.row)" >查看</el-button>
          <el-divider direction="vertical"></el-divider>

          <!--					<el-button type="text" @click="editHandle(scope.row.id)" v-if="hasAuth('sys:field:update')">编辑</el-button>-->
          <el-button type="text" @click="editHandle(scope.row.id)" >编辑</el-button>
          <el-divider direction="vertical"></el-divider>

          <template>
            <!--						<el-popconfirm title="这是一段内容确定删除吗？" @confirm="delHandle(scope.row.id)" v-if="hasAuth('sys:field:delete')">-->
            <el-popconfirm title="这是一段内容确定删除吗？" @confirm="delHandle(scope.row.id)" >
              <el-button type="text" slot="reference">删除</el-button>
            </el-popconfirm>
          </template>

        </template>
      </el-table-column>

    </el-table>

    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 20, 50, 100]"
        :current-page="current"
        :page-size="size"
        :total="total">
    </el-pagination>

    <!--新增对话框-->
    <el-dialog
        title="新增模型"
        :visible.sync="dialogVisible"
        width="600px"
        :before-close="handleClose">

      <el-form :model="editForm" :rules="editFormRules" ref="editForm">

        <el-form-item label="模型名称"  prop="modelName" label-width="100px">
          <el-input v-model="editForm.modelName" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="字段树ID" prop="fieldTreeId" label-width="105px" >
          <el-input-number v-model="editForm.fieldTreeId" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>

        <el-form-item label="索引" prop="index" label-width="105px" >
          <el-input-number v-model="editForm.index" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>

        <el-form-item label="父模型ID" prop="parentModelId" label-width="105px" >
          <el-input-number v-model="editForm.parentModelId" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>

        <el-form-item label="备注"  prop="remark" label-width="100px">
          <el-input v-model="editForm.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('editForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('editForm')">确 定</el-button>
      </div>
    </el-dialog>

  </div>

</template>

<script>
export default {
  name: "Field",
  data() {
    return {
      searchForm: {},
      editForm: {},
      editFormRules: {
        modelName: [
          {required: true, message: '请输入模型名称', trigger: 'blur'}
        ],
        fieldTreeId:[
          {required: true, message: '请输入字段树ID', trigger: 'blur'}
        ],
        index: [
          {required: true, message: '请输入索引', trigger: 'blur'}
        ],
        parentModelId: [
          {required: true, message: '请输入父模型ID', trigger: 'blur'}
        ],
        remark:[
          {required: false, message: '请输入备注', trigger: 'blur'}
        ]
      },
      dialogVisible: false,
    }
  },
  created() {

  },
  methods: {
    getFieldList() {

    },
    delHandle(id) {

      var ids = []

      if (id) {
        ids.push(id)
      } else {
        this.multipleSelection.forEach(row => {
          ids.push(row.id)
        })
      }

      console.log(ids)

      this.$axios.post("/sys/user/delete", ids).then(res => {
        this.$message({
          showClose: true,
          message: '恭喜你，操作成功',
          type: 'success',
          onClose:() => {
            this.getUserList()
          }
        });
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialogVisible = false
      this.editForm = {}
    },
    handleClose() {
      this.resetForm('editForm')
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios.post('/sys/user/' + (this.editForm.id?'update' : 'save'), this.editForm)
              .then(res => {

                this.$message({
                  showClose: true,
                  message: '恭喜你，操作成功',
                  type: 'success',
                  onClose:() => {
                    this.getUserList()
                  }
                });

                this.dialogVisible = false
              })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  }
}
</script>

<style scoped>

.el-pagination {
  float: right;
  margin-top: 22px;
}

.container{
  float: left;
  width: 100%;
  height: 100px;
  background: red;
}
</style>